export default {
	data() {
		return {
			pageList: [],
			hasMoreData: true
		}
	},
	props: {
		urlObj: Object
	},
	mounted() {
		this.getListData()
	},
	watch: {
		urlObj() {
			this.pageList = []
			this.getListData()
		}
	},
	methods: {
		//获取列表数据
		getListData() {
			this.request({
				url: this.urlObj.url,
				data: this.urlObj.param
			}).then(result => {
				if (result.res.videowp.length === 0) {
					this.hasMoreData = false
					uni.showToast({
						title: "没有更多数据了",
						icon: "none"
					})
					return;
				}
				this.pageList = [...this.pageList, ...result.res.videowp]
			})
		},
		//到达底部
		handleScrolltolower() {
			if (this.hasMoreData) {
				this.urlObj.param.skip += this.urlObj.param.limit
				this.getListData()
			} else {
				uni.showToast({
					title: "没有更多数据了",
					icon: "none"
				})
			}

		},
		handleItemClick(item) {
			getApp().globalData.videoObj = item
			uni.navigateTo({
				url: "/pages/video-detail/video-detail"
			})
		}
	}
}
